div {
  box-sizing: border-box;
}
.breadcrumb {
  padding-left: 20px;
  margin-bottom: 0px;
}
#help_tooltip {
  font-size: 20px;
  position: absolute;
  top: 70px;
  left: 270px;
  color: #ccc;
  /*background-color: orange;*/
  z-index: 10;
  transition: .5s;
}
#help_tooltip:hover {
  color: #ff9800;
  transition: .5s;
}
.tooltip-inner {
  max-width: 270px;
  /* If max-width does not work, try using width instead */
  width: 270px;
  text-align: left;
}
.btn-info {
  background-color: #00BCD4 !important;
  border-color: #00BCD4 !important;
  font-family: 'Source Sans Pro';
}
.btn-info.inactive {
  background-color: #66d6e5 !important;
  border-color: #66d6e5 !important;
}
.btn-info.inactive:focus {
  outline: none;
  cursor: default;
  background-color: #66d6e5 !important;
  border-color: #66d6e5 !important;
  opacity: 1;
  box-shadow: none;
}
.btn-info.inactive:hover {
  cursor: default;
  background-color: #66d6e5 !important;
  border-color: #66d6e5 !important;
  opacity: 1;
}
.btn-info:hover {
  opacity: .6;
}
.btn-warning {
  background-color: #FF9800 !important;
  border-color: #FF9800 !important;
  font-family: 'Source Sans Pro';
}
#dropzone > h4 > .glyphicon-question-sign {
