#navbar {
  width: 100%;
  padding: 5px 15px;
  background-color: #556270;
  color: #fff;
}
#navbar #logo {
  font-family: 'Oxygen';
  text-transform: uppercase;
  line-height: 40px;
  font-weight: 300;
  color: #00BCD4;
  font-size: 30px;
  margin-right: 15px;
}
#navbar #fishImg {
  top: 3px;
  position: relative;
  float: right;
}
#navbar .dropdown li {
  text-shadow: none;
}
#navbar .dropdown .divider {
  margin: 5px 0;
}
#navbar #login-buttons {
  float: right;
}
#navbar .glyphicon {
  padding-left: 5px;
  font-size: 12px;
}
#navbar .classlist {
  position: absolute;
}
#filterContainer {
  height: calc(100% - 50px);
  width: 250px;
  border-right: 1px solid #ccc;
  margin: 0px;
  padding: 0px 20px;
  background-color: white;
  overflow: scroll;
}
#bubbleContainer {
  position: absolute;
  bottom: 0px;
  right: 0px;
  height: calc(100% - 50px);
  width: calc(100% - 250px);
  overflow: hidden;
  border: none;
  background-color: white;
}
.bubble,
.group_bubble {
  /*opacity: .85; */
  position: absolute;
  text-align: center;
  vertical-align: middle;
  transition: background-color 1s;
}
.group_bubble {
  pointer-events: none;
}
.group_bubble > .bubble_text {
  font-size: 17px !important;
  line-height: 20px !important;
  margin-top: 5px !important;
  font-weight: 400 !important;
  cursor: pointer !important;
  pointer-events: none !important;
}
.bubble:hover {
  cursor: pointer;
  opacity: 1;
}
.bubble:hover {
  cursor: pointer;
}
#buttons {
  position: absolute;
  top: 70px;
  right: 20px;
  z-index: 10;
}
.edit-col-container {
