.MeteorToys input,
.MeteorToys label,
.MeteorToys select,
.MeteorToys button,
.MeteorToys textarea
{
  margin:0;
  border:0;
  padding:0;
  display:inline-block;
  vertical-align:middle;
  white-space:normal;
  background:none;
  line-height:1;


  /* Browsers have different default form fonts */
  font-size:13px;
  font-family:Arial;
}

/* Remove the stupid outer glow in Webkit */
.MeteorToys input:focus
{
  outline:0;
}

/* Box Sizing Reset
-----------------------------------------------*/

/* All of our custom controls should be what we expect them to be */
.MeteorToys input,
.MeteorToys textarea
{
  -webkit-box-sizing:content-box;
  -moz-box-sizing:content-box;
  box-sizing:content-box;
}

/* These elements are usually rendered a certain way by the browser */
.MeteorToys button,
.MeteorToys input[type=reset],
.MeteorToys input[type=button],
.MeteorToys input[type=submit],
.MeteorToys input[type=checkbox],
.MeteorToys input[type=radio],
.MeteorToys select
{
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
  box-sizing:border-box;
}

/* Text Inputs
-----------------------------------------------*/

.MeteorToys input[type=date],
.MeteorToys input[type=datetime],
.MeteorToys input[type=datetime-local],
.MeteorToys input[type=email],
.MeteorToys input[type=month],
.MeteorToys input[type=number],
.MeteorToys input[type=password],
.MeteorToys input[type=range],
.MeteorToys input[type=search],
.MeteorToys input[type=tel],
.MeteorToys input[type=text],
.MeteorToys input[type=time],
.MeteorToys input[type=url],
.MeteorToys input[type=week]
{
}

/* Button Controls
-----------------------------------------------*/

.MeteorToys input[type=checkbox],
.MeteorToys input[type=radio]
{
  width:13px;
  height:13px;
}

/* File Uploads
-----------------------------------------------*/

.MeteorToys input[type=file]
{

}

/* Search Input
-----------------------------------------------*/

/* Make webkit render the search input like a normal text field */
.MeteorToys input[type=search]
{
  -webkit-appearance:textfield;
  -webkit-box-sizing:content-box;
}

/* Turn off the recent search for webkit. It adds about 15px padding on the left */
::-webkit-search-decoration
{
  display:none;
}

/* Buttons
-----------------------------------------------*/

.MeteorToys button,
.MeteorToys input[type="reset"],
.MeteorToys input[type="button"],
.MeteorToys input[type="submit"] {
  /* Fix IE7 display bug */
  overflow:visible;
  width:auto;
}

/* IE8 and FF freak out if this rule is within another selector */
.MeteorToys ::-webkit-file-upload-button {
  padding:0;
  border:0;
  background:none;
}

/* Textarea
-----------------------------------------------*/

.MeteorToys textarea {
  /* Move the label to the top */
  vertical-align:top;

  /* Turn off scroll bars in IE unless needed */
  overflow:auto;
}

/* Selects
-----------------------------------------------*/

.MeteorToys select {

}

.MeteorToys select[multiple] {
  /* Move the label to the top */
  vertical-align:top;
}

/* Standard Font */

  @font-face {
      font-family: Liberation Mono;
      src: url('../public/LiberationMono-Regular.ttf');
  }

  @font-face {
      font-family: Liberation Mono;
      src: url('../public/LiberationMono-Bold.ttf');
      font-weight: bold;
  }



