/* ORB UI */

  .MeteorToys_orb {
    height: 46px;
    width: 46px;
    float: left;
    margin-left: 7px;

    margin-top: 0px;

    pointer-events:all;
    transition: .25s;
    overflow: hidden;

    position: relative;
    cursor: default;
    width: 320px;
    height: 320px;
    max-width: 600px;
    max-height: 600px;
    border-radius: 0 0 12px 12px;
    border-radius: 24px 24px 12px 12px;
    box-sizing: border-box;
  }

  .MeteorToys_orb_condensed {
    height: 46px !important;
    width: 46px !important;
    border-radius: 24px;
    cursor: pointer;
    margin-top: 7px;
  }

  .MeteorToys_orb_wrapper {
    margin: 7px;
    border-radius: 5px;
    height: calc(100% - 14px);
    overflow: hidden;
    margin-top: 74px;
    transition: .25s;
  }

